.App {
  text-align: center;

}

#main {
  background:url('https://images.pexels.com/photos/2004161/pexels-photo-2004161.jpeg?cs=srgb&dl=pexels-markus-spiske-2004161.jpg&fm=jpg');
  background-attachment: fixed;
  background-size: cover;
  /* opacity: 0.8; */
}


html {
  scroll-behavior: smooth;
}
body {
  font-family: 'Montserrat', 'Prompt';
  
}



h1,h3 {
  font-family: 'Ubuntu';
  font-weight: 700;
}
p{
  font-family: 'Prompt';
}

.card  {
 margin-bottom: 2rem;
}

.card h5{
  
  /* padding:1rem; */
  text-align: center;
  background-color:#282c34;
  color:aliceblue;
  border-radius: 5px;
  padding:5px;
  font-family: 'Ubuntu';
}
/* font-family: 'Prompt', sans-serif; */
/* font-family: 'Ubuntu', sans-serif; */

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.service-section {
  background:url('https://images.pexels.com/photos/5453837/pexels-photo-5453837.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940');
  background-attachment: fixed;
  background-size:cover;
  border-radius: 10px;
}

.bg-service {
  background:rgba(255, 255, 255, 0.5);
  border-radius: 10px;
  color:#282c34;
}